<script>
  import { createEventDispatcher } from "svelte";
  import HotspotDescription from "./../HotspotDescription.svelte";
  import HotspotConnectionLine from "./HotspotConnectionLine.svelte";

  export let modelViewer = "";
  export let hotspot = "";
  export let backCopy = "";
  export let backArrow = "";
  export let model = "";
  export let lights = "";
  export let background = "";
  export let checkmark = "";
  export let blueCheckmark = "";

  export let zoomifyGlass = "";
  export let landscapeImg = "";
  export let zoomifyGlassShadow = "";

  let zoomScreenTL = new TimelineMax();
  let hotspotHoverTL = new TimelineMax();
  let hotspotRolloutTL = new TimelineMax();

  let showDescription = false;
  let id = -1;

  let modelTime = 0;
  const dispatch = createEventDispatcher();

  function isVisible() {
    dispatch("isVisible", true);
  }

  function openVideoHandler() {
    dispatch("open-video", {
      active: true,
      actualId: id,
    });
  }

  function showDescriptionHandler(elementId) {
    showDescription = true;
    id = elementId;
    clickHS(elementId);
  }

  function closeDescriptionHandler() {
    id = -1;
    showDescription = false;
    clickHS(id);
  }

  function clearAnimationHandler() {
    zoomScreenTL.pause("reset");
  }

  function animationZoomifyGlass() {
    zoomScreenTL = new TimelineMax();
    zoomScreenTL
      .add("reset")
      .from(
        glassContainer,
        0.3,
        { force3D: true, z: 0.01, opacity: 0, scaleX: 0, scaleY: 0, y: "120" },
        0.3
      )
      .to(glassContainer, 0.3, { force3D: true, scaleX: 1.2, scaleY: 1.2 }, 0.3)
      .to(
        glassContainer,
        0.3,
        { force3D: true, z: 0.01, opacity: 1, scaleX: 1, scaleY: 1, y: "0" },
        0.4
      )
      .add("slide")
      .to(glassContainer, 4, {  x: "-60" }, "slide")
      .to(EV_Inmersive, 4, {  x: "80" }, "slide");
  }

  function showAll() {
    let animation01 = document.getElementById("animation01");
    let animation02 = document.getElementById("animation02");
    let animation03 = document.getElementById("animation03");
    let animation04 = document.getElementById("animation04");
    let animation05 = document.getElementById("animation05");
    let animation06 = document.getElementById("animation06");

    animation01.setAttribute("data-visible", "");
    animation02.setAttribute("data-visible", "");
    animation03.setAttribute("data-visible", "");
    animation04.setAttribute("data-visible", "");
    animation05.setAttribute("data-visible", "");
    animation06.setAttribute("data-visible", "");

    animation01.setAttribute("style","display:visible;")
    animation02.setAttribute("style","display:visible;")
    animation03.setAttribute("style","display:visible;")
    animation04.setAttribute("style","display:visible;")
    animation05.setAttribute("style","display:visible;")
    animation06.setAttribute("style","display:visible;")
    let allBluecheckmark = document.getElementsByClassName("blue-checkmark");
    let allcheckmark = document.getElementsByClassName("white-checkmark");

    for (var i = 0, len = allBluecheckmark.length; i < len; i++) {
      allBluecheckmark[i].style.opacity = 0;
      allcheckmark[i].style.opacity = 1;
    }
  }

  function hoverHandler() {
    this.lastChild.style.color = "#ffffff";
    hotspotHoverTL = new TimelineMax();
    hotspotHoverTL
      .add("init")
      .to(
        this.getElementsByClassName("blue-checkmark"),
        0.2,
        { opacity: 1, scale: 1.2 },
        "init"
      )
      .to(
        this.getElementsByClassName("white-checkmark"),
        0.5,
        { opacity: 0, scale: 1, ease: Expo.easeOut },
        "init"
      );
    if (this.lastChild.classList.contains("hotspot-copy")) {
      hotspotHoverTL.to(
        this.lastChild,
        0.3,
        {
          background:
            "linear-gradient(90deg, rgba(0,0,0, 0.15) 0%, rgba(0,0,0,1) 90%)",
        },
        "init"
      );
    } else {
      hotspotHoverTL.to(
        this.lastChild,
        0.3,
        {
          background:
            "linear-gradient(90deg, rgba(0, 0, 0, 1) 50%, rgba(0, 0, 0, 0.15) 100%)",
        },
        "init"
      );
    }
  }

  function rollOutHandler() {
    if (this.lastChild.style) this.lastChild.style.color = "#dddddd";
    hotspotRolloutTL = new TimelineMax();
    hotspotRolloutTL
      .add("init")
      .to(
        this.getElementsByClassName("white-checkmark"),
        0.2,
        { opacity: 1, scale: 1 },
        "init"
      )
      .to(
        this.getElementsByClassName("blue-checkmark"),
        0.5,
        { opacity: 0, scale: 1.2, ease: Expo.easeOut },
        "init"
      );
    if (this.lastChild.classList && this.lastChild.classList.contains("hotspot-copy")) {
      hotspotRolloutTL.to(
        this.lastChild,
        0.3,
        {
          background:
            "linear-gradient(90deg, rgba(0, 0, 0, 0) 2%, rgba(0, 0, 0, 0.5) 40%)",
        },
        "init"
      );
    } else {
      hotspotRolloutTL.to(
        this.lastChild,
        0.3,
        {
          background:
            "linear-gradient(90deg, #000000 0%, rgba(0, 0, 0, 0.2) 100%",
        },
        "init"
      );
    }
  }

  function playRange(start, time) {
    let animationPause;

    modelViewer.play();
    modelViewer.currentTime = start;
    animationPause = setTimeout(function () {
      modelViewer.pause();
      open = false;
    }, time);
  }

  function clickHS(el) {
    // .style.cursor = "pointer";

    let animation01 = document.getElementById("animation01");
    let animation02 = document.getElementById("animation02");
    let animation03 = document.getElementById("animation03");
    let animation04 = document.getElementById("animation04");
    let animation05 = document.getElementById("animation05");
    let animation06 = document.getElementById("animation06");

    clearAnimationHandler();
    showAll();
    modelViewer.resetTurntableRotation();
    modelViewer.autoRotate = false;
    modelViewer.cameraControls = false;
    modelViewer.fieldOfView = "40deg";
    modelViewer.cameraTarget="0m 25m 0m"

    switch (el) {
      case 0:
        //Awake in a flash
        modelViewer.cameraTarget="20m 15m -20m"
        modelViewer.cameraOrbit = "30deg 78deg 180m";
        playRange(19, 2000);
        animation02.setAttribute("style","display:none;")
        break;
      case 1:
        //Immersive viewing
        modelViewer.cameraOrbit = "5deg 80deg 160m";
        modelViewer.cameraTarget="0m 30m 0m"
        animationZoomifyGlass();
        playRange(7, 2000);
        animation01.setAttribute("style","display:none;")
        animation05.setAttribute("style","display:none;")

        break;
      case 2:
        //Get thing done fast 
        modelViewer.cameraOrbit = "00deg 10deg 160m";
        modelViewer.cameraTarget="0m 50m 0m"
        animation01.setAttribute("style","display:none;")
        animation02.setAttribute("style","display:none;")

        playRange(13, 2000);
        break;
      case 3:
        //Unplug for longer
        modelViewer.cameraOrbit = "00deg 85deg 160m";
        modelViewer.cameraTarget="20m 15m -20m"

        animation03.setAttribute("style","display:none;")

        playRange(2, 100);
        break;
      case 4:
        //Fast, realible connections
        modelViewer.cameraOrbit = "340deg 80deg 160m";
        animation01.setAttribute("style","display:none;")
        animation02.setAttribute("style","display:none;")
        animation03.setAttribute("style","display:none;")

        playRange(23, 2000);

        break;
      case 5:
        //Universal cable connectivity
        playRange(2, 100);
        modelViewer.cameraOrbit = "300deg 75deg 160m";
        modelViewer.cameraTarget="0m 20m -20m"
        animation03.setAttribute("style","display:none;")
        animation05.setAttribute("style","display:none;")
        break;
      default:
        console.log("default");
        modelViewer.cameraOrbit = "330deg 78deg 160m";
        modelViewer.cameraTarget="0m 27.5m 0m"
        modelViewer.autoRotate = true;
        modelViewer.cameraControls = true;
        playRange(2, 100);
    }
  }

  (() => {
    let interval = self.setInterval(() => {
      modelTime = modelViewer.currentTime;
      if (modelViewer.currentTime >= 2) {
        modelViewer.pause();
        clearInterval(interval);
      }
    }, 100);
  })();
</script>

