          data-position="-50m 11.5m -20m"
          data-normal="-0.6m 0m 0.5m"
          data-visibility-attribute="visible"
          id="animation06">
          <div
            class="showDescriptionHandler"
            on:click={() => showDescriptionHandler(5)}
            on:mouseenter={hoverHandler}
            on:mouseleave={rollOutHandler}>
            <img
              class="img blue-checkmark noMouseInteraction"
              src={blueCheckmark}
              alt="close-device" />
            <img
              class="img white-checkmark noMouseInteraction"
              src={checkmark}
              alt="close-device" />
            <div class="circle scale-in-center noMouseInteraction" />
            {#if !(id == 5)}
              <div class="hotspot-left">{hotspot[5].copy}</div>
            {/if}
          </div>
          {#if showDescription && id == 5}
            <HotspotConnectionLine
              width="153px"
              margin="-27px 0px 0px -157px"
              rotate="rotate(25deg)" />
            <HotspotDescription
              title={hotspot[5].copy}
              subtitle={hotspot[5].description}
              thumbnail={hotspot[5].url.thumbnail}
              margin={'-266px 0px 0px -394px'}
              {backCopy}
              backArrowIcon={backArrow}
              on:close={closeDescriptionHandler}
              on:open-video={openVideoHandler}
              model={modelViewer}
              rightAligned="true"
              selectedCheckmark={blueCheckmark} />
          {/if}
        </button>
      {/if}
    </model-viewer>
  </div>
</div>
<!-- ANIMATION Zoom -->
<div
  id="animZoom"
  class="noMouseInteraction overlaysFXs"
  data-visibility-attribute="visible">
  <div id="glassContainer">
    <img id="shadow" src={zoomifyGlassShadow} alt="zoomifyShadow" />
    <div id="InmersiveContainer">
      <img id="EV_Inmersive" src={landscapeImg} alt="EV_Inmersive" />
    </div>
    <img id="EV_lens" src={zoomifyGlass} alt="EV_lens" />
  </div>
</div>
