  pointer-events: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  padding-top: 3rem;
  line-height: 1rem;
  width: 100%;
  text-align: center;
  position: absolute;
  z-index: 10;
  overflow: hidden; }
  .header h3 {
    font-weight: 200;
    letter-spacing: 0.05rem;
    color: #FFFFFF;
    position: absolute;
    width: 100%;
    opacity: 0;
    margin: 0 auto; }
  .header .subheadline {
    margin-top: 1.7rem;
    color: #149ED9;
    opacity: 0; }
  .header .down-arrow {
    width: 2rem;
    display: flex;
    margin: auto;
    margin-top: .5rem;
    display: block;
    opacity: 0; }
    .header .down-arrow #arrow01 {
      position: relative; }
    .header .down-arrow #arrow02 {
      position: relative;
      top: -.7rem; }
    .header .down-arrow #arrow03 {
      position: relative;
      top: -1.4rem; }
</style>

<div class="header">
    <h3>{headline}</h3>
    <div id="instructions">
        <p class="subheadline">{subheadline}</p>
        <div class="down-arrow">
            <img id="arrow01" class="img arrow" src={arrow} alt="down arrow" />
            <img id="arrow02" class="img arrow" src={arrow} alt="down arrow" />
            <img id="arrow03" class="img arrow" src={arrow} alt="down arrow" />
        </div>
    </div>
</div>